  /* ==========================================================================
     LESSON OVERVIEW
     ========================================================================== */
  .lesson-overview {
    height: 91.65vh;
    position: relative;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .lesson-overview::before {
    content: "";
    position: absolute;
    inset: 0;
    background: var(--color-overlay);
    z-index: 0;
  }
  p {
    background: none;
  }
  .text-center {
    text-align: center;
  }
  
  /* ==========================================================================
     WRAPPER
     ========================================================================== */
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: var(--spacing-medium);
    padding: var(--spacing-medium);
    width: 70%;
    position: relative;
    z-index: 1;
    margin: 0 auto;
  }
  @media (max-width: var(--breakpoint-desktop)) {
    .wrapper {
      display: flex;
      flex-direction: column;
      padding: var(--spacing-medium);
    }
  }
  
  /* ==========================================================================
     WRAPPER COMPONENT
     ========================================================================== */
  .wrapper-component {
    border-radius: 15px;
    height: 50%;
    padding: 1rem;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  /* ==========================================================================
     RIGHTY (the futuristic style panel)
     ========================================================================== */
  .righty {
    background: var(--gradient-primary);
    flex-direction: column;
    border: 2px solid transparent;
    border-radius: 1rem;
    height: 100%;
    position: relative; /* needed so child overlay is contained */
    z-index: 1;
    color: var(--color-text);
    width: 100%;
    margin: auto;
    max-height: calc(100vh - 56px);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  .righty:hover {
    transform: scale(0.98);
    border-radius: 18px;
    border-color: var(--color-accent);
    box-shadow: 0 0 20px 2px rgba(0, 250, 255, 0.5);
  }
  
  /* ==========================================================================
     CARD & LIST ITEM STYLES
     ========================================================================== */
  .card {
    position: relative;
    background-position: center;
    height: 100%;
    border-radius: 15px;
    background-size: cover;
    cursor: pointer;
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1),
                box-shadow 0.3s ease;
    overflow: hidden;
  }
  .card:hover {
    transform: translateY(-5px) scale(1.02);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.3);
  }
  .card h3 {
    margin: 0.5rem 0;
    color: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    text-shadow: 
      0 0 5px #000,
      0 0 10px #000,
      0 0 15px #000,
      6px 5px 4px #000;
  }
  .card p {
    font-size: 1rem;
    color: #ccc;
  }
  .card.next {
    animation: bounce 2s infinite, shine 2s infinite;
  }
  .cardbright {
    background: linear-gradient(90deg, #fcd535, #ff76ce);
  }
  .carddark {
    background: linear-gradient(90deg, #0ABAB5, #fff0eb);
  }
  .card-content-top {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0.5rem 1rem;
    color: #fff;
    font-weight: 800;
    font-style: oblique;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
    z-index: 10;
    overflow: hidden;
  }
  .card-content-top::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 200%;
    height: 100%;
    background: linear-gradient(
      120deg,
      transparent,
      rgba(255, 255, 255, 0.3),
      transparent
    );
    transform: skewX(-25deg);
    animation: shine 2s infinite;
  }
  .card.unlocked {
    cursor: default;
    opacity: 0.8;
  }
  
  /* List items (modules, lessons, etc.) */
  .card-item {
    display: flex;
    align-items: center;
    gap: var(--spacing-small);
    padding: var(--spacing-small);
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.1);
    transition: transform 0.2s ease;
    cursor: pointer;
  }
  @media (hover: hover) and (pointer: fine) {
    .card-item:hover {
      background-color: rgba(255, 255, 255, 0.05);
      transform: scale(1.02);
      padding-left: 0.2rem;
    }
  }
  .module-num {
    color: var(--color-accent);
    font-size: 12px;
    text-shadow:
      -1px -1px 0 #000,
       0   -1px 0 #000,
       1px -1px 0 #000,
       1px  0   0 #000,
       1px  1px 0 #000,
       0    1px 0 #000,
      -1px  1px 0 #000,
      -1px  0   0 #000;
  }
  .listImg {
    height: 4rem;
    width: 4rem;
    border-radius: 8px;
    margin-right: var(--spacing-small);
  }
  .listImg-placeholder {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    border-radius: 8px;
    background: rgba(255,255,255,0.05);
  }
  
  /* Image container overlays */
  .image-container,
  .card-image-container {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .image-container img,
  .card-image-container img {
    object-fit: cover;
    margin: auto;
  }
  .dark-overlay {
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(0.2px);
  }
  .access-overlay {
    position: absolute;
    filter: drop-shadow(0 2px 6px rgba(0, 255, 255, 0.8));
  }
  .access-icon {
    width: 32px;
    height: 32px;
  }
  .card-content,
  .card-text {
    padding: var(--spacing-small);
    z-index: 2;
  }
  
  /* ==========================================================================
     ANIMATIONS (bounce, shine, etc.)
     ========================================================================== */
  @keyframes float {
    0% { transform: translateY(0); }
    50% { transform: translateY(-3px); }
    100% { transform: translateY(0); }
  }
  @keyframes bounce {
    0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
    40% { transform: translateY(-10px); }
    60% { transform: translateY(-5px); }
  }
  @keyframes shine {
    0% { filter: brightness(1); }
    50% { filter: brightness(1.4); }
    100% { filter: brightness(1); }
  }
  @keyframes pulse {
    0% { transform: scale(1); }
    50% { transform: scale(1.1); }
    100% { transform: scale(1); }
  }
  @media (prefers-reduced-motion: reduce) {
    * {
      animation: none !important;
      transition: none !important;
    }
  }
  
  /* ==========================================================================
     MODULES LIST
     ========================================================================== */
  .modules-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--spacing-small);
    padding: var(--spacing-small);
  }
  @media (max-width: var(--breakpoint-tablet)) {
    .modules-list {
      grid-template-columns: 1fr;
    }
  }
  
  /* ==========================================================================
     ERROR STATES
     ========================================================================== */
     .error-box {
      max-width: 100%;
      height: 91.4vh;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      font-size: 1.2rem;
      background: url("https://firebasestorage.googleapis.com/v0/b/anglomova-429e2.appspot.com/o/sign%20in.webp?alt=media&token=f5467655-c015-48cc-88f9-446582ddf29f") center/cover no-repeat;
  }
  
  .error-text {
    width: 400px;
    margin: 20px auto; /* centers the box and gives it some vertical spacing */
    padding: 16px 20px; /* top-bottom 16px, left-right 20px */
    background: linear-gradient(
      to right,
      rgba(247, 87, 87, 0.45),
      rgba(247, 87, 87, 0.25) 50%,
      rgba(247, 87, 87, 0.45)
    );
    border-left: 5px solid #f75757;
    /* Keep the left side squared off and round only the right side */
    border-radius: 0 15px 15px 0;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-size: 1.2rem;
    color: #ffffff;
    transform: translateY(5rem);
  }
  
  
  /* ==========================================================================
     RESPONSIVE ADJUSTMENTS
     ========================================================================== */
  @media (max-width: var(--breakpoint-desktop)) {
    .wrapper {
      display: flex;
      flex-direction: column;
      padding: var(--spacing-medium);
    }
    .righty {
      width: auto;
      background: linear-gradient(135deg, #2a2a2a, #1a1a1a);
    }
  }
  @media (max-width: var(--breakpoint-tablet)) {
    body {
      font-size: clamp(0.9rem, 1.5vw + 0.8rem, 1rem);
    }
  }
  @media (max-width: var(--breakpoint-mobile)) {
    body {
      font-size: clamp(0.8rem, 1.2vw + 0.7rem, 0.9rem);
    }
    .card {
      max-width: 100%;
      height: auto;
    }
  }
  
  /* ==========================================================================
     NEW: LOCAL OVERLAY INSIDE .righty
     ========================================================================== */
  .mod-local {
    position: absolute;
    inset: 0; /* fill entire .righty container */
    background: rgba(0, 0, 0, 0.85);
    z-index: 10;
    justify-content: center;
    align-items: center;
    /* If you want scrolling within the box, you can do this: */
    overflow-y: auto;
    /* But be mindful of .righty’s own max-height. */
    scrollbar-width: thin;
    scrollbar-color: #FFD700 #000;
  }
  .righty::-webkit-scrollbar-track {
      margin-top: 1rem;    /* creates space at the top of the track */
      margin-bottom: 1rem; /* creates space at the bottom of the track */
    }
 
  .mod-local-cont {
    background: var(--gradient-dark);
    border-radius: 16px;
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.5);
    position: relative;
    gap: var(--spacing-medium);
    padding: var(--spacing-medium);
  }
  
  /* Header inside the local overlay */
  .mod-local-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: var(--spacing-small);
    border-bottom: 2px solid var(--color-accent);
  }
  .mod-local-close-btn {
    background: none;
    border: none;
    color: #fff;
    font-size: 1.2rem;
    cursor: pointer;
    padding: 0.2rem 0.5rem;
    transition: color 0.3s ease;
  }
  .mod-local-close-btn:hover {
    color: var(--color-accent);
  }
  
  /* “Lessons” & “Workbooks” local lists inside overlay */
  .lessons-local-list,
  .workbooks-local-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--spacing-small);
  }
  @media (max-width: var(--breakpoint-tablet)) {
    .lessons-local-list,
    .workbooks-local-list {
      grid-template-columns: 1fr;
    }
  }
  
  /* Module info inside overlay */
  .mod-local-info {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-small);
    background: rgba(255, 255, 255, 0.1);
    padding: var(--spacing-small);
    border-radius: 8px;
  }
  .mod-local-desc {
    /* Potential styling for full description */
  }
  .mod-local-short {
    /* Potential styling for short description */
  }
  